.formulario {
	p {
		font-size: 1.4rem;
		color: $grisOscuro;
		margin: 2rem 0 0 0;
	}

	legend {
		font-size: 2rem;
		color: $grisOscuro;
	}
	label {
		display: block;
		font-weight: $bold;
		text-transform: uppercase;
	}
	input:not([type="submit"]),
	textarea {
		padding: 1rem;
		display: block;
		width: 100%;
		background-color: $blanco;
		border: 1px solid $gris;
		border-radius: 1rem;
		margin-bottom: 2rem;
	}
}
textarea {
	height: 20rem;
}

.forma-contacto {
	max-width: 25rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
input[type="radio"] {
	margin: 0 !important;
	width: auto;
}
